<template lang="pug">
  .toolbar
    v-toolbar.topbar(
      fixed
      :light="toolbarLight"
      :style="toolbarStyle"
      )
      v-toolbar-items(
        v-for="item in itemsSocial"
        :key="item.link"
        )
        v-btn.ml-4.mr-4(
          large
          icon
          flat
          target="_BLANK"
          :href="item.link"
          :title="item.title"
          )
          v-icon(
            :class="item.class"
            )
      v-spacer
      v-toolbar-items(
        v-if="!isMobile"
        v-for="item in itemsToolbar"
        :key="item.link"
        )
        v-btn(large flat v-scroll-to="item.link") {{ item.text }}
</template>

<script>
import ToolbarItems from '@/assets/ToolbarItems';
import SocialItems from '@/assets/SocialItems';

export default {
  props: {
    toolbarLight: {
      type: Boolean,
      required: true,
    },
    toolbarStyle: {
      type: Object,
      required: true,
    },
  },
  data: () => ({
    isMobile: false,
    itemsToolbar: ToolbarItems,
    itemsSocial: SocialItems,
  }),
  mounted() {
    this.setIsMobile();
  },
  methods: {
    setIsMobile() {
      this.isMobile = window.innerWidth < 620;
      this.$forceUpdate();
    },
  },
  watch: {
    'window.innerWidth': 'setIsMobile',
  },
};
</script>

<style lang="stylus">
  .topbar
    z-index 3
</style>



